@charset "UTF-8";
/* Agrega tus estilos personalizados aquí */
.login-container {
  padding: 20px;
  text-align: center;
}
.login-container form {
  width: 50%;
  margin: 0 auto;
}
.login-container h2 {
  font-size: 24px;
  margin-bottom: 20px;
}
.login-container .form-group {
  margin-bottom: 15px;
  text-align: left;
}
.login-container label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}
.login-container input[type=text],
.login-container input[type=password] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
}
.login-container .submit-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 3px;
  cursor: pointer;
}
.login-container .error-message {
  color: #ff0000;
  margin-top: 10px;
  font-weight: bold;
}